html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 86px;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  display: block;
}

.header {
  background-color: #4CB8B3;
  padding: 6rem;
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-evenly;
  }
}

@media (min-width: 768px) {
  .header-nav {
    displat: flex;
    gap: 10rem;
  }
}
.header-nav a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2.4rem;
  color: #FFFFFF;
  text-decoration: none;
}
.header-nav a:hover {
  color: rgba(205, 205, 205, 0.8039215686);
}

.titulos .titulo-h2 {
  position: relative;
  left: 5rem;
  color: #FFFFFF;
  font-weight: 800;
  font-size: 5rem;
  text-align: left;
}
@media (min-width: 768px) {
  .titulos .titulo-h2 {
    font-size: 7.5rem;
  }
}
.titulos .titulo-p {
  font-size: 3rem;
  color: #FFFFFF;
  text-align: left;
  text-align: center;
}
@media (min-width: 768px) {
  .titulos .titulo-p {
    font-size: 3.2rem;
    text-align: left;
    margin-left: 2%;
  }
}
.titulos .btn-header {
  border: none;
  background-color: #4CB8B3;
  color: #f54756;
  font-size: 2.5rem;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  margin-top: 2rem;
  text-transform: uppercase;
  font-weight: bold;
  width: 110px;
  margin-left: 50%;
  transform: translateX(-50%);
}
.titulos .btn-header:hover {
  color: #000080;
  cursor: pointer;
}
@media (min-width: 768px) {
  .titulos .btn-header {
    margin-left: 5%;
  }
}

.main {
  background-color: #4ac5bf;
}
.main .main-sobremi {
  text-align: center;
  color: #FFFFFF;
  font-weight: 800;
}
.main .sobre-cont {
  display: block;
  display: flex;
  justify-content: center;
}

.img-p {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  padding-top: 5rem;
  font-size: 2.5rem;
  display: block;
}

.main-carac {
  padding-top: 5rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  font-weight: 700;
  font-size: 2.3rem;
}

li {
  list-style: none;
}

.compromiso {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.compromiso .compromiso-h2 {
  padding-top: 17rem;
  color: #FFFFFF;
  font-size: 4rem;
  text-transform: capitalize;
  padding-left: 2rem;
  font-weight: 800;
}

.compromiso-li {
  text-decoration: none;
  text-transform: capitalize;
  color: rgba(205, 205, 205, 0.8039215686);
  font-size: 2rem;
  font-weight: 700;
  padding-left: 5rem;
}
.compromiso-li p {
  color: #FFFFFF;
  text-transform: capitalize;
}

.plan {
  background-color: #4CB8B3;
  color: #FFFFFF;
  padding-left: 1rem;
  font-size: 1.5rem;
  text-align: center;
}
.plan h2 {
  font-size: 3rem;
  padding-bottom: 3rem;
}
.plan .planes {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  padding-bottom: 3rem;
}
.plan .planes h3 {
  font-size: 2rem;
}
.plan .planes p {
  color: #FFFFFF;
  font-weight: 700;
  padding: 2rem;
}
.plan .planes a {
  color: #FFFFFF;
  text-transform: capitalize;
  font-size: 1.5rem;
  font-weight: 700;
}

.contacto-content {
  color: #FFFFFF;
  padding-bottom: 2rem;
}
.contacto-content h2 {
  text-align: center;
}
.contacto-content p {
  color: #FFFFFF;
}

.formulario {
  background-color: #4ac5bf;
}
.formulario h2 {
  color: #FFFFFF;
  text-align: center;
  padding-bottom: 2rem;
}

.campo {
  padding-left: 5rem;
  display: flex;
  margin-bottom: 2rem;
  gap: 1rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 800;
}

.campo__label {
  flex: 0 0 1;
  text-align: right;
}

.campo__field {
  flex: 0.2;
  align-items: center;
  font-size: 1.5rem;
  border: none;
}

.campo__field--textarea {
  border: none;
  height: 10rem;
}

.campo__label---email {
  padding-left: 2rem;
}

.submit {
  text-decoration: none;
  border: none;
  margin-bottom: 4rem;
  margin-left: 6rem;
  font-size: 1.5rem;
  color: #000000;
  font-weight: 800;
}

.footer {
  background-color: #4CB8B3;
}
.footer .social__p {
  color: #000000;
  text-align: center;
  font-weight: 800;
  text-transform: capitalize;
  font-size: 2.3rem;
}
.footer .footer-p {
  text-align: center;
  color: #000000;
  font-weight: 800;
  text-transform: capitalize;
  font-size: 2.3rem;
}
.footer .social__a {
  padding-top: 5rem;
  gap: 1rem;
  display: flex;
  margin: 0 auto;
  max-width: 22%;
  min-width: 23%;
}