@use "variables" as v;

html{
    font-size: 62.5%; //*1rem=10px
    box-sizing: border-box;
    scroll-behavior: smooth;

}
*,
*:before,
*:after{
    box-sizing: inherit;
}
body{
    font-family: v.$fuelte_principal;
    font-size: 1.6rem;

    &.fijar-body{
        overflow: hidden;
    }
    &.body-scroll{
        padding-top: 86px;
    }
}
p{
    font-size: 2rem;
    color:v.$negro;
    line-height: 1.5;
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a{
    text-decoration: none;
}

h1,h2,h3{
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

img{
    width: 100%;
    height: auto;
    max-width: 100%;
    display: block;
}